<template>
    <v-navigation-drawer 
        v-if="$vuetify.display.mobile"
        id="app-drawer"
        width="250"
        v-model="drawer" 
        :rail="rail"
        dark 
        app 
        order="1"
        elevation="0"
    >
        <v-list-item class="d-flex justify-center align-center">
            <v-list-item-title 
                v-if="!rail"
                class="text-h7 d-flex align-center break-word"
            >
                <div class="d-flex flex-column align-center">
                    <v-avatar class="my-3 ml-2" size="40">
                        <v-img src="../assets/profile.jpg" alt="Profile" />
                    </v-avatar>
                    <v-card>
                        <v-card-text>
                            <div class="mx-auto text-center">
                                <h3>{{ user.fullName }}</h3>
                                <p class="text-caption mt-1">
                                    {{ user.email }}
                                </p>
                            </div>
                        </v-card-text>
                    </v-card>
                </div>
            </v-list-item-title>
        </v-list-item>
        <DefaultList :items="computedItems"/>
    </v-navigation-drawer>

    <v-app-bar 
        color=primary
        app 
        dark
        elevation="0"
    >
        <v-app-bar-nav-icon 
            v-if="$vuetify.display.mobile"
            @click="appBarNav"
        />
            <v-row v-if="!$vuetify.display.mobile" class="flex-grow-1 ma-3 ml-10" align="center" no-gutters>
                <v-menu min-width="200px">
                    <template v-slot:activator="{ props }">
                        <v-btn
                            icon
                            v-bind="props"
                        >
                            <v-avatar size="large">
                                <v-img src="../assets/profile.jpg" alt="Profile" />
                            </v-avatar>
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-text>
                            <div class="mx-auto text-center">
                                <v-avatar class="my-3 ml-2" size="40">
                                    <v-img src="../assets/profile.jpg" alt="Profile" />
                                </v-avatar>
                                <h3>{{ user.fullName }}</h3>
                                <p class="text-caption mt-1">
                                    {{ user.email }}
                                </p>
                                <v-divider class="my-3"></v-divider>
                                <v-btn
                                    variant="text"
                                    to="/profile"
                                    rounded
                                >
                                    자세히 보기
                                </v-btn>
                                <v-divider class="my-3"></v-divider>
                                <v-btn
                                    variant="text"
                                    rounded
                                >
                                    닫기
                                </v-btn>
                            </div>
                        </v-card-text>
                    </v-card>
                </v-menu>
                <span class="ml-3 font-weight-bold">
                    개발자 김혁준의 자기소개 | 포트폴리오
                </span>
            </v-row>
            <v-spacer></v-spacer>
            <v-menu :close-on-content-click="false" min-width="200px">
                <template v-slot:activator="{ props }">
                    <v-btn v-bind="props" class="mr-3">
                        <v-tooltip
                            activator="parent"
                            location="bottom"
                        >테마 변경</v-tooltip>
                        <v-icon size="28">mdi-cog-outline</v-icon>
                    </v-btn>
                </template>
                <v-card class="pa-4">
                    <v-card-text>테마 변경</v-card-text>
                    <v-radio-group v-model="themeProxy">
                        <v-radio :value="'light'">
                            <template #label>
                                <div class="d-inline-flex align-center theme-label">
                                    <v-icon class="mr-2" size="18">mdi-white-balance-sunny</v-icon>
                                    <span>Light</span>
                                </div>
                            </template>
                        </v-radio>
                        <v-radio :value="'dark'">
                            <template #label>
                                <div class="d-inline-flex align-center theme-label">
                                    <v-icon class="mr-2" size="18">mdi-weather-night</v-icon>
                                    <span>Dark</span>
                                </div>
                            </template>
                        </v-radio>
                        <v-radio :value="'system'">
                            <template #label>
                                <div class="d-inline-flex align-center theme-label">
                                    <v-icon class="mr-2" size="18">mdi-desktop-tower-monitor</v-icon>
                                    <span>System</span>
                                </div>
                            </template>
                        </v-radio>
                    </v-radio-group>
                    <!-- <v-card-actions class="justify-end mt-2">
                        <v-btn variant="text" @click="menu = false">닫기</v-btn>
                    </v-card-actions> -->
                </v-card>
            </v-menu>
        </v-app-bar>
    <v-app-bar v-if="!$vuetify.display.mobile"
        elevation="0" height="70" density="compact">
      <!-- 탭 형태로 메뉴 나열 -->
      <v-container class="px-4">
        <v-tabs v-model="current" density="compact">
          <v-tab
            v-model="current"
            v-for="(it, i) in computedItems"
            :key="i"
            :to="it.to"
            :value="it.to"
            replace
            exact
          >
            <v-tooltip
                activator="parent"
                location="bottom"
            >{{ it.title }}</v-tooltip>
            <div v-if="it.title === 'HOME'">
                <v-icon :icon="it.icon"></v-icon>
            </div>
            <div v-else>
                <v-icon :icon="it.icon"></v-icon>
                {{ it.title }}
            </div>
          </v-tab>
        </v-tabs>
      </v-container>
    </v-app-bar>
    <v-main class="app-bk-color mt-n4">
        <v-container>
            <v-spacer />
            <router-view />
        </v-container>
    </v-main>
</template>
<script>
    import { mapActions } from 'vuex'
    import DefaultList from '../layouts/menu/List.vue'
    export default {
        name: 'DefaultLayout',
        data(){
            return {
                drawer: true,
                rail: false,
                user: {
                    fullName: '김혁준',
                    email: 'dkdlelgurwns@naver.com'
                },
                theme: 'light',
            }
        },
        computed: {
             themeProxy: {
                get () {
                    return this.$store.state.theme.mode   // 'light' | 'dark'
                },
                set (val) {
                    this.setMode(val) // Vuex action → mutation → main.js에서 Vuetify 반영
                }
            },
            computedItems() {
                let items = [
                    { title: 'HOME', icon: 'mdi-home', to: '/dashboard', divider: true, rail: this.rail },
                    { title: '프로젝트 소개', icon: 'mdi-view-dashboard', to: '/project', divider: true, rail: this.rail },
                    { title: '프로필', icon: 'mdi-account', to: '/profile', divider: true, rail: this.rail },
                    /* { title: '국가기록원', icon: 'mdi-map', to: '/toji', rail: this.rail }, */
                ];
                return items;
            },
            current: {
                get () { 
                    const p = this.$route.path
                    if (p === '/project' || p.startsWith('/project/')) return '/project'
                    return p
                },
                set (val) {
                    if (val && val !== this.$route.path) this.$router.push(val)
                }
            }
        },
        methods: {
            logout() {
                this.dialog = true;
            },
            appBarNav() {
                if(!this.$vuetify.display.mobile) {
                    this.rail = !this.rail;
                }else{
                    this.drawer = !this.drawer;
                }
            },
            ...mapActions('theme', ['setMode']),
        },
        created() {

        },
        components: {
            DefaultList,
        }
    }
</script>
<style>
.v-navigation-drawer {
    border-right: none !important;
    box-shadow: none !important;
}
.break-word {
    white-space: normal !important;
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
    text-align: center;
}
</style>